* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  color: #333;
  background-color: #f9f9f9;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  display: flex;
}

.container {
  text-align: center;
  background-color: #fff;
  border-radius: 8px;
  width: 90%;
  max-width: 600px;
  padding: 20px;
  box-shadow: 0 4px 8px #0000001a;
}

.breed-select {
  background-color: #f4f4f4;
  border: 1px solid #ddd;
  border-radius: 4px;
  width: 100%;
  margin: 10px 0;
  padding: 10px;
  font-size: 16px;
  transition: border-color .3s;
}

.breed-select:focus {
  border-color: #6c63ff;
  outline: none;
}

.loader {
  color: #6c63ff;
  justify-content: center;
  align-items: center;
  margin: 15px 0;
  font-size: 16px;
  display: flex;
}

.loader:before {
  content: "";
  border: 3px solid #6c63ff;
  border-top-color: #0000;
  border-radius: 50%;
  width: 20px;
  height: 20px;
  margin-right: 8px;
  animation: spin;
}

.hidden {
  display: none;
}

.visible {
  display: block;
}
